body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow: hidden; */
  touch-action: pan-left, pan-right, pan-up, pan-down;
}

* {
  touch-action: pan-left, pan-right, pan-up, pan-down;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app-navbar ~ .app-header, .app-navbar ~ .app-main-container {
    margin-left: 0;
}

.app-header:before, .app-main-container:before {
  content: "";
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.app-main-container {
  position: relative;
  margin: 0 auto;
  display: block;
  clear: both;
  float: unset;
  right: 0;
  min-height: 100vh;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: whitesmoke;
}

.app-content {
  position: relative;
  display: block;
  padding: 30px;
}

.card {
  -webkit-box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);
  box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);
  margin-bottom: 30px;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  /* min-width: 300px !important;
  min-height: 100px !important; */
  max-width: 350px;
}

.card .card-header {
  background-color: transparent;
  border-bottom: 1px solid #f1f1f1;
  padding: 20px 25px;
  position: relative;
}

.card .card-header h5 {
  margin-bottom: 0;
  color: #000;
  font-size: 17px;
  font-weight: 400;
  display: inline-block;
  margin-right: 10px;
  line-height: 1.1;
  position: relative;
}

.card .card-header h5:after {
  content: "";
  background-color: #04a9f5;
  position: absolute;
  left: -25px;
  top: 0;
  width: 4px;
  height: 20px;
}


.card .card-block, .card .card-body {
  padding: 30px 25px;
}

.Commandes .table tr:first-child td, .User-Lists .table tr:first-child td {
  border-top: 0;
}

.Commandes .table td, .User-Lists .table td {
  vertical-align: middle;
}

.table td, .table th {
  border-top: 1px solid #eaeaea;
  white-space: nowrap;
  padding: 1.05rem 0.75rem;
}

.card.Screen-Preview {
  background-color: rgb(28, 48, 83);
  width: 350px;
  height: 350px;
}

.container.Preview-List {
  padding: 2%;
  height: 100%;
}

.Preview-OrderNumber {
  font-size: 40px;
  height: 100%;
  line-height: 1;
  vertical-align: middle;
  color:  rgb(245, 234, 221) !important;
}

.Preview-Row {
  height: 12.5%;
}

.card-block.Screen-Preview {
  height: 100%;
}

.flexValues {
  width: 100px;
}

#btn_status_order {
  width:70px;
}

#btn_delete_order {
  width:30px;
}


@media only screen and (max-width: 450px) {
  .card.Screen-Preview {
    background-color: rgb(28, 48, 83);
    width: 60vw;
    height: 60vw;
  }
  
  .container.Preview-List {
    padding: 2%;
    height: 100%;
  }
  
  .Preview-OrderNumber {
    font-size: 7vw;
    height: 100%;
    line-height: 1;
    vertical-align: middle;
    color:  rgb(245, 234, 221) !important;
  }
  
  .Preview-Row {
    height: 12.5%;
  }
  
  .card-block.Screen-Preview {
    height: 100%;
  }
  
  .flexValues {
    width: 45%;
    height: max-content;
  }
  
  #btn_status_order {
    width:72%;
    vertical-align: middle;
    height: max-content;
  }
  
  #btn_delete_order {
    width:28%;
    vertical-align: middle;
    height: max-content;
  }

  .FlexContainer {
    height: max-content;
  }

  .FlexContainer .btn {
    font-size: 1.1rem !important;
  }
}